// This file is shared across the demos.
import Link from 'next/link';
import React from 'react';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import { request } from 'graphql-request';



 class FerryMenu extends React.Component {
  state = {data: {}};

  async componentDidMount() {
    try{
      const query = `{
        allRoutes {
          id
          routeName
        }
      }`;
      const URL = 'https://ferrytrackerserver.now.sh/graphql';
      const res = await request(URL, query);
      console.log(`All routes fetched. Count: ${res.allRoutes.length}`);
      this.setState({data: res}) ;
    } catch(err) {
      console.error(err)
    }
    };

  render() {
    return (
  <List>
    {console.log(this.state.data)}
    {this.state.data.allRoutes.map(route => (
    <Link
      prefetch
      as={`post?id=${route.routeName}`}
      href={{ pathname: '/post', query: { id: route.routeName } }}
    >
     <ListItem button key={route.id}>
      <ListItemText primary={route.routeName} />
      </ListItem>
    </Link>
))}
  </List>
);
  }
  }


export default FerryMenu;